.blogPostContainer{
    width: 70%;
}
.blogHeader{
    text-align: center;
}
.blogCategory{
    display: block;
    font-size: 14px;
    padding: 10px 0;

}
.postTitle{
    letter-spacing: .5px;
    text-transform: capitalize;
    font-size: 30px;
    margin: 0;
    font-weight: 400;
}

.postedBy{
    color: #6c6c6c;
    font-size: 15px;
    font-style: italic;
    padding: 10px 0 ;
    display: block;
}
.postimageContainer{
width: 100%;
padding : 10px;
box-sizing: border-box;
}

.postimageContainer img{
max-width: 100%;
max-height: 100%;
}
.postContent{
    margin: 10px;
}
.postContent h3{
    font-size: 14px;
    color: #6c6c6c;
    font-weight: 300;
    letter-spacing: 1px;
    padding-bottom: 20px 0 ;
}